<template>
  <div id="user">
    <!--  -->
    <div id="user_main">
      <!--  -->
      <div id="crumbs">
        <ul id="crumbs_nav">
          <li><router-link to="/"></router-link></li>
          <span>></span>
          <li></li>
        </ul>
      </div>

      <!--  -->
      <div id="from_user">
        <!--  -->
        <div id="from_user_l">
          <!--  -->
          <div id="ids">
            <p> :</p>
            <p>{{ userInfo.id }}</p>
          </div>
          <!--  -->
          <div id="tel">
            <p> :</p>
            <p>{{ getTel }}</p>
          </div>

          <!--  -->
          <div id="media">
            <p> :</p>
            <input
              type="text"
              placeholder=""
              v-model="userInfo.name"
            />
          </div>

          <!--  -->
          <div id="media_recomment">
            <p> :</p>
            <textarea
              name=""
              id="media_re"
              cols="30"
              rows="10"
              placeholder=""
              v-model="userInfo.intro"
            ></textarea>
          </div>

          <!--  -->
          <div id="email">
            <p> :</p>
            <input
              type="email"
              placeholder=""
              v-model="userInfo.email"
            />
          </div>

          <!--  -->
          <button id="save" @click="edit_userInfo"></button>
        </div>
        <!--  -->
        <div id="from_user_r">
          <!--  -->
          <div id="user_img">
            <!--  -->
            <img
              :src="userInfo.photo"
              style="border-radius: 5%"
              alt=""
            />
            <label for="files"></label>
            <input
              id="files"
              ref="files"
              type="file"
              hidden
              @change="updata_photo"
            />
          </div>
        </div>

        <!--  -->
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
          :append-to-body="true"
          @opened="open_end"
        >
          <!--  -->
          <!--  -->
          <div id="img_pre">
            <img :src="pre_img" style="height: 300px" ref="img_p" />
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button :disabled="is_commit" type="primary" @click="on_commit"
              > </el-button
            >
          </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import "cropperjs/dist/cropper.css";
import Cropper from "cropperjs";
import { user_info, edit_user, edit_userPhoto } from "../../apis/user";
export default {
  name: "user",
  data() {
    return {
      // 
      userInfo: [],
      // 
      dialogVisible: false,
      // 
      pre_img: "",
      // cropper
      cropper: null,

      // 
      is_commit: false,
    };
  },
  created() {
    // 
    this.getUserInfo();
  },

  methods: {
    // 
    getUserInfo() {
      let tokens = localStorage.getItem("token");
      // 
      user_info(tokens)
        .then((data) => {
          let datas = data.data.data;

          // 
          this.userInfo = datas;

          // Vuex
          let uname = this.userInfo.name;

          this.$store.commit("tran", uname);
        })
        .catch((err) => {
          console.log(err);
        });
    },

    // 
    edit_userInfo() {
      let tokens = localStorage.getItem("token");
      // 
      edit_user(tokens, {
        // 
        name: this.userInfo.name,
        intro: this.userInfo.intro,
        email: this.userInfo.email,
      })
        .then((data) => {
          console.log(data);
          // 
          this.$message({
            type: "success",
            message: "!",
          });
          // 
          this.getUserInfo();
        })
        .catch((err) => {
          console.log(err);
          // 
          this.$message({
            type: "warring",
            message: "!",
          });
        });
    },

    // 
    updata_photo() {
      // imgDom
      const file = this.$refs.files;
      // files
      let img_dom = file.files[0];
      // Html5
      const img_url = window.URL.createObjectURL(img_dom);
      // 
      this.pre_img = img_url;

      // 
      this.$refs.files.value = "";
      // 
      this.dialogVisible = true;
    },

    // 
    open_end() {
      const img = this.$refs.img_p;
      // console.log(img);
      // blob: //192.168.1.3:8080/ada01cd1-7679-47dd-896b-e77f5ae29b9c
      // 
      // http:
      if (this.cropper) {
        this.cropper.replace(this.pre_img);
        return;
      }
      this.cropper = new Cropper(img, {
        aspectRatio: 1,
        viewMode: 1,
        crop: function (e) {
          // console.log(e.detail.x);
          // console.log(e.detail.y);
          // console.log(e.detail.width);
          // console.log(e.detail.height);
          // console.log(e.detail.rotate);
          // console.log(e.detail.scaleX);
          // console.log(e.detail.scaleY);
        },
      });
    },

    // 
    on_commit() {
      // ------
      this.is_commit = true;
      // 
      this.cropper.getCroppedCanvas().toBlob((bold) => {
        // console.log(bold);
        // FormData
        const fd = new FormData();
        // fdphoto---
        fd.append("photo", bold);
        // 
        let tokens = localStorage.getItem("token");
        // 
        edit_userPhoto(tokens, fd)
          .then((data) => {
            // 
            this.dialogVisible = false;
            // ----
            this.is_commit = false;
            // 
            this.$message({
              type: "success",
              message: "!",
            });
            // $emit$bussu
            this.$bus.$emit("updata_user");
            // 
            this.getUserInfo();
          })
          .catch((err) => {
            // 
            this.dialogVisible = false;
            // ----
            this.is_commit = false;
            // 
            this.$message({
              type: "warring",
              message: "!",
            });
          });
      });
    },
  },
  // 
  computed: {
    getTel: {
      // 
      get() {
        let num = this.userInfo.mobile;
        // 
        if (num) {
          let num1 = num.substring(0, 3);
          let num2 = num.substring(8, 4);
          let str = "****";
          let tel = num1 + str + num2;
          return tel;
        } else {
          return "";
        }
      },
    },
  },
};
</script>


