<template>
  <div id="user_comment">
    <!--  -->
    <div id="comment_main">
      <!--  -->
      <div id="crumbs">
        <ul id="crumbs_nav">
          <li><router-link to="/"></router-link></li>
          <span>></span>
          <li></li>
        </ul>
      </div>

      <!--  -->
      <div id="comment_table">
        <table>
          <!--  -->
          <thead>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </thead>
          <!--  -->
          <tbody>
            <tr v-for="(item, i) in comment_data" :key="i">
              <!--  -->
              <td>{{ item.title }}</td>
              <!--  -->
              <td>{{ item.total_comment_count }}</td>
              <!--  -->
              <td>{{ item.fans_comment_count }}</td>
              <!--  -->
              <td>{{ item.comment_status ? "" : "" }}</td>
              <!-- or -->
              <td>
                <!--  -->
                <button
                  :disabled="is_btn"
                  :class="{
                    btn_on: !item.comment_status,
                    btn_off: item.comment_status,
                  }"
                  @click="updata_comment(item.id, item.comment_status)"
                >
                  {{ item.comment_status ? "" : "" }}
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <!--  -->
        <el-pagination
          :disabled="is_btn"
          id="fen"
          @current-change="page_chang"
          :current-page.sync="page"
          background
          layout="prev, pager, next"
          :total="total_page"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { articles } from "../../apis/article";
import { comment_status } from "../../apis/comment";
export default {
  name: "comment",
  data() {
    return {
      // 
      comment_data: [],

      // 
      page: 1,

      // 
      total_page: 0,
      // 
      is_btn: false,
    };
  },
  created() {
    this.getComment();
  },
  methods: {
    // 
    getComment() {
      // 
      this.is_btn = true;
      let tokens = localStorage.getItem("token");
      // 
      articles(tokens, {
        // query
        response_type: "comment",
        // 
        page: this.page,
      })
        .then((data) => {
          let datas = data.data.data.results;
          // data
          this.comment_data = datas;

          //  /
          let num_page = Math.ceil(data.data.data.total_count / 10);
          // *10 
          this.total_page = num_page * 10;

          // 
          this.is_btn = false;
        })
        .catch((err) => {
          console.log(err);
          // 
          this.is_btn = false;
        });
    },

    // 
    updata_comment(id, isStatus) {
      let pl_id = id.toString();
      // 
      this.is_btn = true;
      let tokens = localStorage.getItem("token");
      // 
      let is_allow = !isStatus;
      // -----
      comment_status(tokens, { allow_comment: is_allow }, { article_id: pl_id })
        .then((data) => {
          // 
          let is = is_allow ? "" : "";
          this.$message({
            type: "success",
            message: `${is}`,
          });
          // 
          this.is_btn = false;
          // 
          this.getComment();
        })
        .catch((err) => {
          // 
          this.is_btn = false;
          // 
          let is = is_allow ? "" : "";
          this.$message({
            type: "warring",
            message: `${is}`,
          });
          console.log(err);
        });
    },
    // 
    page_chang() {
      this.getComment();
    },
  },
};
</script>
