<template>
  <!---->
  <div id="issue">
    <!--  -->
    <div id="main_issue">
      <!--  -->
      <div id="crumbs">
        <ul id="crumbs_nav">
          <li><router-link to="/"></router-link></li>
          <span>></span>
          <li></li>
        </ul>
      </div>

      <!--  -->
      <div id="public">
        <!--  -->
        <div id="public_main">
          <!--  -->
          <div id="title">
            <p></p>
            <!--  -->
            <input
              maxlength="30"
              minlength="5"
              type="text"
              id="public_title"
              placeholder=""
              v-model="info.title"
            />
          </div>
          <!--  -->
          <div id="content">
            <p></p>
            <!--  -->
            <el-tiptap
              v-model="info.content"
              width="80%"
              height="400px"
              :extensions="extensions"
              lang="zh"
            />
          </div>
          <!--  -->
          <div id="cover">
            <!--  -->
            <div id="public_cover">
              <!--  -->
              <p></p>
              <input
                type="radio"
                id="one"
                v-model="info.cover.type"
                :value="1"
                name="_cover"
              /><label for="one"></label>
              <input
                type="radio"
                id="three"
                v-model="info.cover.type"
                :value="3"
                name="_cover"
              /><label for="three"></label>
              <input
                type="radio"
                id="no_w"
                v-model="info.cover.type"
                :value="0"
                name="_cover"
              /><label for="no_w"></label>
              <input
                type="radio"
                id="auto"
                v-model="info.cover.type"
                :value="-1"
                name="_cover"
              /><label for="auto"></label>
            </div>
            <!--  -->
            <!-- <Cover :num="info.cover.type" /> -->
            <div id="img_show">
              <Cover :num="info.cover.type" v-for="(item, i) in obj" :key="i" />
            </div>
          </div>
          <!--  -->
          <div id="activity">
            <p></p>
            <div id="public_activity">
              <select name="" id="channel" v-model="info.channel_id">
                <option value=""></option>
                <option v-for="(item, i) in article" :key="i" :value="item.id">
                  {{ item.name }}
                </option>
              </select>
            </div>
          </div>
          <!-- button -->
          <div id="btn">
            <button
              id="commit_channel"
              @click="request(false)"
              :disabled="is_button"
            >
              
            </button>
            <button
              id="commit_draft"
              @click="request(true)"
              :disabled="is_button"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import cover from "./component/cover.vue";
import {
  //
  ElementTiptap,
  //  extensions-----
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  Image,
  ListItem,
  BulletList,
  OrderedList,
} from "element-tiptap";
// 
import {
  public_article,
  channel,
  id_article,
  edit_article,
} from "../../apis/article";
import { user_img } from "../../apis/user_matter";
export default {
  name: "issue",
  // el-tiptap
  components: {
    "el-tiptap": ElementTiptap,
    Cover: cover,
  },
  data() {
    return {
      // 
      article: [],

      // 
      is_button: false,

      // 
      info: {
        // 
        channel_id: "",

        // img
        cover: {
          type: 1,
          images: [],
        },

        // 
        title: "",
        // 
        content: "",
      },

      // ele-tiptap
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // 
        new Underline({ bubble: true, menubar: false }), // 
        new Italic(),
        new Strike(),
        new Image({
          // images---uploadRequest
          uploadRequest(flie) {
            let tokens = localStorage.getItem("token");

            // FormDatacontent-typeimagedata
            let data = new FormData();
            data.append("image", flie);
            // user_img---promies-----
            return user_img(tokens, data).then((data) => {
              let url = data.data.data.url;
              // ------
              return url;
            });
          },
        }),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
      ],
    };
  },

  created() {
    // 
    this.get_article();
    // 
    if (this.$route.query.id) {
      // id
      this.put_article(this.$route.query.id);
    }
  },
  mounted() {
    this.$bus.$on("getImg", (url) => {
      //  infourl
      this.info.cover.images[0] = url;
    });
  },
  beforeDestroy() {
    // 
    this.$bus.$off("getImg");
  },
  methods: {
    // ------
    request(isCaoGao) {
      // 
      this.is_button = true;
      // 
      let tokens = localStorage.getItem("token");

      // -----
      let title_length = this.info.title.trim().length;
      let content_length = this.info.content.trim().length;
      // 
      if (title_length >= 5 && title_length <= 30 && content_length > 0) {
        // 
        if (this.$route.query.id) {
          let id = this.$route.query.id;
          // 
          edit_article(tokens, id, this.info, isCaoGao)
            .then((data) => {
              alert("");
              // 
              // 
              this.is_button = false;
              this.$router.push("/conest");
            })
            .catch((err) => {
              // console.log(err);
              // 
              this.is_button = false;
            });
        }
        // 
        else {
          // 
          // ------tokens-----this.info--body
          // isCaoGao-----query
          public_article(tokens, this.info, isCaoGao)
            .then((data) => {
              // id
              console.log(data.data.data.id.toString());
              // 
              this.is_button = false;
              console.log("");
              this.$router.push("/conest");
            })
            .catch((err) => {
              console.log("" + err);
              // 
              this.is_button = false;
            });
        }
      } else {
        alert("");
        // 
        this.is_button = false;
        // 
        this.info.title = "";
        this.info.content = "";
      }
    },

    // 
    get_article() {
      // 
      let tokens = localStorage.getItem("token");
      // 
      channel(tokens).then((data) => {
        let arr = data.data.data.channels;
        // id
        // id
        arr.push({ name: "", id: undefined });
        // vue
        this.article = arr;
      });
    },

    // 
    put_article(id) {
      // 
      let tokens = localStorage.getItem("token");
      // id
      id_article(tokens, id)
        .then((data) => {
          let datas = data.data.data;
          //  info
          this.info = datas;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

  computed: {
    obj: {
      get() {
        if (this.info.cover.type <= 0 && this.info.cover.type == 3) {
          return 0;
        }
        return this.info.cover.type;
      },
    },
  },
};
</script>

