<script>
import { articles, channel, del_channel } from "../../apis/article";
// 
export default {
  name: "const",
  data() {
    return {
      // ------
      articles: [],

      // 
      channel_info: [],
      // id
      channl_status: undefined,
      // 
      value1: [],

      // 
      time_li: 1,

      // 
      pages_updata: 0,

      // 
      total_count: 0,

      // 
      is_no_data: "",

      // 
      from_status: null,

      // loading
      is_load: false,
    };
  },

  created() {
    // tableloading
    this.is_load = true;
    // 
    this.articles_updata();
    // 
    this.channel_data();
  },

  methods: {
    // 
    articles_updata() {
      // 
      let tokens = localStorage.getItem("token");
      // --------
      articles(tokens, {
        // 
        status: this.from_status,

        // 
        channel_id: this.channl_status,

        // 
        // ----,
        begin_pubdate: this.value1 ? this.value1[0] : undefined,
        // 
        end_pubdate: this.value1 ? this.value1[1] : undefined,

        //
        page: this.time_li,
        //
        per_page: 20,
      })
        .then((data) => {
          let datas = data.data.data;
          // data
          this.articles = datas.results;

          // 
          this.sum(datas);

          // 
          this.page_sum(datas);

          // loading
          this.is_load = false;
        })
        .catch((err) => {
          console.log("" + err);
          // 2s
          setTimeout(() => {
            this.is_load = false;
            alert("");
          }, 2000);
        });
    },

    // 
    channel_data() {
      // 
      let tokens = localStorage.getItem("token");
      // 
      channel(tokens).then((data) => {
        let arr = data.data.data.channels;
        // id
        // id
        arr.push({ name: "", id: undefined });
        // vue
        this.channel_info = arr;
      });
    },

    // 
    sum(data) {
      // console.log(data.total_count);

      // 0;
      if (data.total_count == 0) {
        this.is_no_data = false;
      } else {
        this.is_no_data = true;
      }

      //total_count
      this.total_count = data.total_count;
    },

    // 
    yema() {
      // loading
      this.is_load = true;
      // 
      this.articles_updata();
    },

    // 
    page_sum(data) {
      // 20----
      let yema = Math.ceil(data.total_count / 20);

      //  pages_updata
      this.pages_updata = yema * 10;
    },

    // 
    commit_btn() {
      // -----
      // loading
      this.is_load = true;
      //id,,
      this.articles_updata();
    },

    // 
    edit_btn(art_id) {
      // idissue
      this.$router.push(`/issue?id=${art_id}`);
    },

    // 
    del_btn(value) {
      // 
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          let tokens = localStorage.getItem("token");
          // id-----.toString()
          del_channel(value.toString(), tokens)
            .then((data) => {
              // 
              this.articles_updata();
            })
            // ----
            .catch((err) => {
              console.log("" + err);
            });

          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>

