<template>
  <div id="matter">
    <!--  -->
    <div id="matter_main">
      <!--  -->
      <div id="crumbs">
        <ul id="crumbs_nav">
          <li></li>
          <span>></span>
          <li></li>
        </ul>
      </div>
      <!-- img -->
      <div id="crumbs_bottom">
        <!-- -- -->
        <div id="coll">
          <!--  -->
          <div id="coll_l">
            <div id="all" ref="all" @click="all_matter">
              <p></p>
            </div>
            <div id="star" ref="star" @click="star_matter">
              <p></p>
            </div>
          </div>

          <!--  -->
          <div id="coll_r">
            <el-upload
              class="upload-demo"
              action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
              :headers="onheader"
              name="image"
              :on-success="success"
              :on-error="err"
              list-type="picture"
            >
              <el-button
                size="small"
                type="primary"
                style="width: 100px; hight: 50px"
                ></el-button
              >
            </el-upload>
          </div>
        </div>
        <!--  -->
        <div id="desplay_img" style="position: relative">
          <el-table
            v-loading="loading"
            style="position: absolute; width: 100%; height: 140%"
          >
          </el-table>
          <el-col
            :lg="6"
            :md="8"
            :sm="12"
            :xs="24"
            v-for="(item, i) in img_matter"
            :key="i"
            ><el-image
              style="width: 240px; height: 200px"
              :src="item.url"
              fit="cover"
            >
            </el-image>
            <div id="icon">
              <!--  -->
              <i
                :class="{
                  'el-icon-star-off': !item.is_collected,
                  'el-icon-star-on': item.is_collected,
                }"
                @click="star(item.id, item.is_collected, item.isLoading)"
              ></i>
              <!--  -->
              <i class="el-icon-delete" @click="del(item.id)"></i>
            </div>
          </el-col>
        </div>
        <!--  -->
        <div id="page_number">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="total"
            :current-page.sync="page"
            @current-change="changPage"
          >
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { get_matter, star_matter, del_matter } from "../../apis/user_matter";
import { updata_token } from "../../apis/user";
export default {
  name: "matter",
  data() {
    let user_token = window.localStorage.getItem("token");
    return {
      // 
      img_matter: [],

      // 
      is_img_status: false,

      // 
      total: 0,
      // loading
      loading: true,
      // 
      page: 1,
      // 
      onheader: {
        Authorization: `Bearer ${user_token}`,
      },
    };
  },
  created() {
    // 
    this.getUserMatter();
  },
  methods: {
    // 
    getUserMatter() {
      // loading
      this.loading = true;
      let tokens = localStorage.getItem("token");
      get_matter(tokens, {
        // 
        collect: this.is_img_status,
        // 
        page: this.page,
        // 
        per_page: 12,
      })
        .then((data) => {
          // 
          let datas = data.data.data.results;

          // data
          this.img_matter = datas;

          // 
          let total = data.data.data.total_count;

          // ---
          let i = Math.ceil(total / 12);

          // data
          this.total = i * 10;
          // loading
          this.loading = false;
          // console.log(datas, i);
          // token
          // updata_token(tokens).then((data) => {
          //   console.log(data);
          // });
        })
        .catch((err) => {
          // loading
          this.loading = false;
          console.log(err);
        });
    },
    // 
    all_matter() {
      // 
      this.is_img_status = false;
      // 
      this.$refs.all.style.backgroundColor = "skyblue";
      this.$refs.star.style.backgroundColor = "rgb(42, 38, 61)";
      // 
      this.getUserMatter();
    },
    // 
    star_matter() {
      // 
      this.is_img_status = true;
      // 
      this.$refs.star.style.backgroundColor = "skyblue";
      this.$refs.all.style.backgroundColor = "rgb(42, 38, 61)";
      // 
      this.getUserMatter();
    },
    // 
    changPage() {
      // 
      this.getUserMatter();
    },
    // 
    success() {
      this.$alert("", "", {
        confirmButtonText: "OK",
        callback: () => {
          this.$message({
            type: "success",
            message: ``,
          });
        },
      });
    },
    // 
    err() {
      this.$alert("", "", {
        confirmButtonText: "OK",
        callback: () => {
          this.$message({
            type: "warning",
            message: ``,
          });
        },
      });
    },
    // 
    star(id, isStar) {
      // loading
      this.loading = true;
      let user_token = localStorage.getItem("token");
      star_matter(user_token, id, { collect: !isStar })
        .then((data) => {
          // console.log(data);
          // loading
          this.loading = false;
          // 
          if (!isStar) {
            this.$message({
              type: "success",
              message: "",
            });
          } else {
            this.$message({
              type: "success",
              message: "",
            });
          }

          // 
          this.getUserMatter();
        })
        .catch((err) => {
          // loading
          this.loading = false;
          console.log(err);
        });
    },
    // 
    del(id) {
      let user_token = localStorage.getItem("teken");
      del_matter(user_token, id)
        .then((data) => {
          console.log(data);
          this.$message({
            type: "success",
            message: "",
          });
          // 
          this.getUserMatter();
        })
        .catch((err) => {
          this.$message({
            type: "warning",
            message: "---",
          });
          console.log(err);
        });
    },
  },
};
</script>

