<template>
  <div id="login">
    <transition name="islogin">
      <div id="war_y" v-show="islogin_y"><p></p></div>
    </transition>
    <transition name="islogin">
      <div id="war_n" v-show="islogin_n"><p></p></div>
    </transition>
    <el-form ref="form" :model="user" id="login_box">
      <div id="log">
        <div id="logo"></div>
        <h1></h1>
      </div>

      <el-form-item>
        <el-input
          ref="tels"
          v-model="user.mobile"
          placeholder=""
          @focus="verify_tel_ent"
          @blur="verify_tel_leave"
        ></el-input>
        <p class="tooltip" ref="tel_p">
          {{ info.tel_p }}
        </p>
      </el-form-item>

      <el-form-item>
        <el-input
          ref="codes"
          type="text"
          v-model="user.code"
          placeholder=""
          @focus="verify_code_ent"
          @blur="verify_code_leave"
        ></el-input>
        <p class="tooltip" ref="code_p">{{ info.code_p }}</p>
      </el-form-item>

      <div id="deal">
        <input type="checkbox" id="isOk" v-model="isCheck_deal" />&nbsp;
        <label for="isOk"></label>
      </div>

      <el-form-item>
        <el-button
          type="primary"
          @click="onSubmit"
          id="btn_login"
          :disabled="isbutton"
          >{{ button_status }}</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

  

<script>
// 
import { login } from "@/apis/user";
export default {
  name: "login",
  data() {
    return {
      user: {
        // 
        mobile: "13911111111",
        // 
        code: "246810",
      },

      // 
      // 
      isCheck_deal: false,
      // 
      islogin_y: false,
      islogin_n: false,
      // 
      button_status: "",
      // 
      isbutton: false,
      // 
      info: {
        tel_p: "",
        code_p: "",
      },
    };
  },

  methods: {
    // ajax
    onSubmit() {
      // 
      let test_tel = this.user.mobile;
      let test_code = this.user.code;
      if (test_tel != "" && test_code != "") {
        if (this.isCheck_deal) {
          // 
          this.isbutton = true;
          this.button_status = "";
          // 
          // const user = JSON.stringify(this.user);
          // ------apis
          login(this.user)
            // 
            .then((data) => {
              let data_user = data.data.data;

              // token
              // this.$bus.$emit("token_user", data_user);

              // Vuex------commitmutations
              // data_userVuex
              this.$store.commit("user_token", data_user);
              // 
              this.islogin_y = true;
              // 
              this.isbutton = false;
              this.button_status = "";
              // 
              setTimeout(() => {
                this.$router.push({
                  name: "index",
                });
              }, 1000);
            })
            // 
            .catch((err) => {
              console.log("" + err);
              // 
              this.islogin_n = true;
              // 
              this.button_status = "";
              this.isbutton = false;
              alert("");
            });

          // 3s
          setTimeout(() => {
            this.islogin_y = false;
            this.islogin_n = false;
          }, 3000);
        } else {
          //
          alert("");
          return;
        }
      } else {
        alert("");
        return;
      }
    },

    // 
    // tel
    // 
    verify_tel_ent() {
      if (this.user.mobile == "") {
        this.$refs.tels.$refs.input.style.borderColor = "yellow";
        this.$refs.tel_p.style.color = "yellow";
        this.info.tel_p = "";
      }
    },
    // 
    verify_tel_leave() {
      // 
      let gz = /^1[34578]\d{9}$/;
      let tel = this.user.mobile.trim();
      if (gz.test(tel)) {
        this.$refs.tels.$refs.input.style.borderColor = "green";
        this.$refs.tel_p.style.color = "green";
        this.info.tel_p = "";
        this.isbutton = false;
      } else if (tel === "") {
        this.info.tel_p = "";
        this.$refs.tels.$refs.input.style.borderColor = "red";
        this.$refs.tel_p.style.color = "red";
        this.user.mobile = "";
      } else {
        this.$refs.tels.$refs.input.style.borderColor = "red";
        this.$refs.tel_p.style.color = "red";
        this.info.tel_p = "";
        this.isbutton = true;
      }
    },

    // code
    // 
    verify_code_ent() {
      if (this.user.mobile == "") {
        this.$refs.codes.$refs.input.style.borderColor = "yellow";
        this.$refs.code_p.style.color = "yellow";
        this.info.code_p = "";
      }
    },
    // 
    verify_code_leave() {
      // 
      let code_test = /^\d{6}$/;
      // 
      let code = this.user.code.trim();
      if (code_test.test(code)) {
        this.$refs.codes.$refs.input.style.borderColor = "green";
        this.$refs.code_p.style.color = "green";
        this.info.code_p = "";
        this.isbutton = false;
      } else if (code === "") {
        this.info.code_p = "";
        this.$refs.codes.$refs.input.style.borderColor = "red";
        this.$refs.code_p.style.color = "red";
        this.user.code = "";
      } else {
        this.$refs.codes.$refs.input.style.borderColor = "red";
        this.$refs.code_p.style.color = "red";
        this.info.code_p = "";
        this.isbutton = true;
      }
    },
  },
};
</script>

