  background-size: cover;

  #login_box {
    padding: 50px 75px 10px 75px;
    background-color: #fff;
    min-width: 500px;
    border-radius: 5px;
    // 
    #log {
      width: 100%;
      height: 75px;
      line-height: 75px;
      // logo
      #logo {
        float: left;
        width: 30%;
        height: 100%;
        background-image: url("./samantha-600x315.jpg");
        background-repeat: no-repeat;
        background-position: 0 -18px;
        background-size: cover;
        background-color: #666;
      }

      h1 {
        float: left;
        width: 70%;
        margin-bottom: 20px;
      }
    }
    // 
    #deal {
      width: 100%;
      margin-bottom: 15px;
    }

    // 
    #btn_login {
      width: 100%;
    }
  }

  // 
  #war_y {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 10%;
    width: 15%;
    height: 40px;
    background-color: #79ed66;
    font-size: 25px;
    text-align: center;
    line-height: 40px;
    color: #fff;
    border-radius: 5px;
  }

  #war_n {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 10%;
    width: 15%;
    height: 40px;
    font-size: 25px;
    background-color: red;
    text-align: center;
    line-height: 40px;
    color: #fff;
    border-radius: 5px;
  }

  @keyframes show {
    0% {
      opacity: 0.1;
      top: 6%;
    }
    100% {
      opacity: 1;
      top: 10%;
    }
  }

  @keyframes show_l {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  .islogin-enter-active {
    animation: show 1s;
  }
  .islogin-leave-active {
    animation: show_l 1s;
  }
}
.tooltip {
  width: 100%;
  height: 15px;
  font-weight: 700;
}
</style>