<template>
  <div id="root">
    <!--  -->
    <div id="side_nav" ref="l_nav">
      <!-- logo -->
      <div id="side_logo">
        <div id="logo"></div>
        <p v-show="isfold"></p>
      </div>

      <!--  -->
      <!--  -->
      <ul>
        <!--  -->
        <!-- / -->
        <li
          :class="{ li_click: item.isclick }"
          v-for="(item, i) in rout"
          :key="i"
          @click="updata_style(i)"
        >
          <router-link :to="'/' + item.to"
            ><i :class="['iconfont', 'icon-' + item.class_icon]"></i>&nbsp;
            <p v-show="isfold">{{ item.text }}</p></router-link
          >
        </li>
      </ul>
    </div>

    <!--  -->
    <div id="side_nav_r" ref="r_nav">
      <!--  -->
      <div id="header_nav">
        <!--  -->
        <div id="firm">
          <i
            :class="{
              iconfont: true,
              'icon-zhedie': isfold,
              'icon-zhedie2': !isfold,
            }"
            @click="fold"
          ></i>
          &nbsp;
          <p>ABC</p>
        </div>

        <!--  -->
        <div id="user_info">
          <div id="head_port">
            <!--  -->
            <img src="./demo_head.jpg" alt="" v-show="is_true" />
            <img :src="user.photo" alt="" v-show="is_false" />
          </div>
          &nbsp;&nbsp;
          <!--  -->
          <!-- -------- -->
          <p @click="push" v-show="is_true"></p>
          <p v-show="is_false">{{ user.name }}</p>
          &nbsp;&nbsp;
          <i class="iconfont icon-linecar105" @click="user_f = !user_f"></i>

          <!--  -->
          <div id="user_facility" v-show="user_f">
            <ul>
              <li></li>
              <li @click="remove_user('', 'green')"></li>
            </ul>
          </div>
        </div>
      </div>

      <!--  -->
      <div id="main">
        <router-view> </router-view>
      </div>
    </div>
    <!--  -->
    <transition name="warring">
      <div id="warring" v-show="isclick_warring">
        <p ref="war_color">{{ war_text }}</p>
      </div>
    </transition>
  </div>
</template>

<script>
// axios
import { user_info } from "@/apis/user";
export default {
  name: "layout",
  data() {
    return {
      // 
      // to
      // class_icon---
      // text----
      // isclick----li
      rout: [
        { to: "", class_icon: "shouye", text: "", isclick: false },
        {
          to: "conest",
          class_icon: "rizhimingxi",
          text: "",
          isclick: false,
        },
        {
          to: "matter",
          class_icon: "icon_huabanfuben",
          text: "",
          isclick: false,
        },
        {
          to: "issue",
          class_icon: "tubiao-",
          text: "",
          isclick: false,
        },
        {
          to: "comment",
          class_icon: "pinglun",
          text: "",
          isclick: false,
        },
        {
          to: "fans",
          class_icon: "weibiaoti-",
          text: "",
          isclick: false,
        },
        {
          to: "user",
          class_icon: "shezhi",
          text: "",
          isclick: false,
        },
      ],

      // 
      user: {},

      // 
      user_f: false,

      // 
      is_true: true,
      is_false: false,

      // 
      isfold: true,
      // 
      isclick_warring: false,
      // 
      war_text: "",
      war_color: "",
    };
  },

  // get
  created() {
    this.getUserInfo();
    // this.$router.push("/");
  },
  //   Dom
  mounted() {
    // -----
    let path = this.$route.path.substring(1);
    this.rout.forEach((item, i) => {
      if (item.to == path) {
        // 
        item.isclick = true;
      }
    });
    //   this.$bus
    // sudata
    this.$bus.$on("updata_user", () => {
      //  
      this.getUserInfo();
    });
  },

  //   vue$bus
  // $bus
  beforeDestroy() {
    this.$bus.$off("updata_user");
  },

  methods: {
    // 
    updata_style(i) {
      //i------
      const rout = this.rout;

      // foreach-----
      rout.forEach((item) => {
        item.isclick = false;
      });

      // 
      rout[i].isclick = true;
    },

    // ----
    push() {
      this.$router.push({
        name: "login",
      });
    },

    //
    getUserInfo() {
      // vuex
      let tokens = this.$store.state.token;

      // 
      if (tokens) {
        localStorage.setItem("token", tokens);
      }
      // 
      let token = localStorage.getItem("token");

      user_info(token)
        // 
        .then((data) => {
          this.is_true = false;
          this.is_false = true;
          this.user = data.data.data;
        })
        //
        .catch((err) => {
          this.is_true = true;
          this.is_false = false;
        });
    },

    // 
    remove_user(text, color) {
      localStorage.removeItem("token");
      // 
      this.warring_click(text, color);
      // 3s
      setTimeout(() => {
        this.$router.push("/login");
      }, 3000);
    },

    //
    fold() {
      if (this.isfold) {
        this.isfold = false;
        this.$refs.l_nav.style.width = "5%";
        this.$refs.r_nav.style.width = "95%";
      } else {
        this.isfold = true;
        this.$refs.l_nav.style.width = "16%";
        this.$refs.r_nav.style.width = "84%";
      }
    },
    // 
    warring_click(text, color) {
      // 
      this.war_text = text;
      // 
      this.$refs.war_color.style.color = color;
      // 
      this.isclick_warring = true;
      // 
      setTimeout(() => {
        this.isclick_warring = false;
      }, 2000);
    },
  },
};
</script>

